.TodoItem {
    display: flex;
    justify-content: center;
    width: 300px;
    align-items: center;
    border: 1px solid black;
    margin-top: 10px;
    padding: 0 10px;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}


.Icon-check--active {
    color: #4caf50;
}


.Icon-delete:hover {
    color: red;
}
  